/**
 * @description to html
 *
 */

import { Element } from 'slate';
import { LinkElement } from './custom-types';

function linkToHtml(elem: Element, childrenHtml: string): string {
  const {
    url,
    target = '_blank',
    style = { color: 'rgb(69, 105, 212)' },
  } = elem as LinkElement;
  const { color } = style;
  return `<a href="${url}" target="${target}" style="color:${color}">${childrenHtml}</a>`;
}

export const linkToHtmlConf = {
  type: 'link',
  elemToHtml: linkToHtml,
};
