/**
 * @description editor 插件，重写 editor API
 *
 */

import { Editor, Transforms, Element, Node, Point } from 'slate';
import { DomEditor, IDomEditor } from '@klein-editor/core';

function deleteHandler(newEditor: IDomEditor): boolean {
  try {
    const { selection } = newEditor;
    if (selection == null) return false;

    const [nodeEntry] = Editor.nodes(newEditor, {
      match: (n) => Element.isElement(n),
      universal: true,
      mode: 'lowest',
    });
    if (!nodeEntry?.length) return false;

    const [node, path] = nodeEntry;
    const isFirstChild = path.join('').replace(/0/g, '') === '';

    if (path.length > 1 && isFirstChild && Node.string(node) === '') {
      const parentNode = Node.parent(newEditor, path);
      Transforms.removeNodes(newEditor, { at: path });
      if (parentNode?.children?.length === 1) {
        newEditor.select(path);
      }
      return true;
    }
  } catch (error) {
    console.log('🚀 ~ file: plugin.ts:31 ~ deleteHandler ~ error', error);
  }
  return false;
}

function withDIV<T extends IDomEditor>(editor: T): T {
  const { insertText, insertBreak, deleteBackward, deleteForward } = editor;
  const newEditor = editor;

  // 重写 insertBreak - 换行时插入 p
  newEditor.insertBreak = () => {
    const { selection } = newEditor;
    if (selection == null) return insertBreak();

    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => DomEditor.checkNodeType(n, 'div:not([data-k-e-type])'),
      universal: true,
    });
    if (!nodeEntry) return insertBreak();

    const DIVElem = nodeEntry[0];
    const DIVPath = DomEditor.findPath(editor, DIVElem);
    const DIVEndLocation = Editor.end(editor, DIVPath);

    if (Point.equals(DIVEndLocation, selection.focus)) {
      // 光标位于 div 最后
      const str = Node.string(DIVElem);
      if (str && str.slice(-1) === '\n') {
        // div 文本最后一个是 \n
        editor.deleteBackward('character'); // 删除最后一个 \n

        // 则插入一个 paragraph
        const p = DomEditor.genEmptyParagraph(editor);
        Transforms.insertNodes(newEditor, p, { mode: 'highest' });
        return;
      }
    }

    // 插入换行符
    insertText('\n');
  };

  // 删除非 p 的文本 elem（如 header blockQuote 等），删除没有内容时，切换为 p
  newEditor.deleteBackward = (unit) => {
    const res = deleteHandler(newEditor);
    if (res) return; // 命中结果，则 return

    // 执行默认的删除
    deleteBackward(unit);
  };

  // 返回 editor ，重要！
  return newEditor;
}

export default withDIV;
