/**
 * @description header menu
 *
 */

import { Editor, Node, Element, Transforms, Text } from 'slate';
import { IDomEditor, t, IButtonMenu, DomEditor } from '@klein-editor/core';
import { BORDER_SVG } from '../../../constants/icon-svg';

class BorderTopMenu implements IButtonMenu {
  readonly title = t('border.title');
  readonly iconSvg = BORDER_SVG;
  readonly tag = 'button';
  readonly mark = 'border';

  /**
   * 获取匹配的 node 节点
   * @param editor editor
   */
  private getMatchNode(editor: IDomEditor): Node | null {
    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => Element.isElement(n),
      universal: true,
      mode: 'lowest',
    });

    if (nodeEntry == null) return null;
    return nodeEntry[0];
  }
  isEmptyText(editor: IDomEditor): boolean {
    let isEmptyText = false;
    const node: any = this.getMatchNode(editor);
    if (
      typeof node === 'object' &&
      node != null &&
      Array.isArray(node.children)
    ) {
      node.children.forEach((item) => {
        if (item.text) isEmptyText = true;
      });
    }
    return isEmptyText;
  }
  /**
   * 选项需要回显，所以获取具体值
   */
  getValue(editor: IDomEditor): string | boolean {
    const curMarks = Editor.marks(editor) as any;
    return curMarks?.style?.border;
  }

  isActive(editor: IDomEditor): boolean {
    const isMark = this.getValue(editor);
    if (!!isMark && !this.isEmptyText(editor)) this.exec(editor);
    return !!isMark;
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) return true;
    return false;
  }

  exec(editor: IDomEditor, value?: string | boolean) {
    const marks = Editor.marks(editor) as any;
    const style = marks?.style || {};
    const { borderWidth, borderStyle, borderColor } =
      editor.getMenuConfig('border');

    const nodeStyle = {
      ...style,
    };
    if (style.border) {
      delete nodeStyle.border;
      Transforms.setNodes(
        editor,
        {
          style: nodeStyle,
        },
        {
          match: (n) => Text.isText(n),
          mode: 'lowest',
        },
      );
    } else {
      nodeStyle.border = `${borderWidth} ${borderStyle} ${borderColor}`;
      editor.addMark('style', nodeStyle);
    }
  }
}

export default BorderTopMenu;
