/**
 * @description undo menu
 *
 */

import { IButtonMenu, IDomEditor, t } from '@klein-editor/core';
import { UNDO_SVG } from '../../../constants/icon-svg';

class UndoMenu implements IButtonMenu {
  title = t('undo.undo');
  iconSvg = UNDO_SVG;
  tag = 'button';
  key = 'undo';

  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    return false;
  }

  isDisabled(editor: IDomEditor): boolean {
    // 自定义需求：无撤销记录，disabled菜单
    const { undos } = editor.history || {};
    if (undos && undos.length > 0) {
      return false;
    } else {
      return true;
    }

    // if (editor.selection == null) return true;
    return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    if (typeof editor.undo === 'function') {
      editor.undo();
    }
  }
}

export default UndoMenu;
