import { Element, Descendant } from 'slate';
import { VNode } from 'snabbdom';
import { addVnodeStyle, setVnodeStyle } from '../../utils/vdom';
import { LinkElement } from './custom-types';

/**
 * 添加样式
 * @param node slate elem
 * @param vnode vnode
 * @returns vnode
 */
export function renderStyle(node: Descendant, vnode: VNode): VNode {
  if (!(Element.isElement(node) && node.type === 'link')) return vnode;
  let styleVnode: VNode = vnode;
  const { style = {} } = node as LinkElement;
  if (style) {
    const { color } = style;
    addVnodeStyle(styleVnode, {
      color: color || 'rgb(69, 105, 212)',
    });
  }

  return styleVnode;
}
