/**
 * @description blockquote menu class
 *
 */

import { Editor, Transforms } from 'slate';
import { IButtonMenu, IDomEditor, DomEditor, t } from '@klein-editor/core';
import { QUOTE_SVG } from '../../../constants/icon-svg';
import { toBHeaderStyle } from '../../header/helper';
import { getMatch } from '../helper';

class BlockquoteMenu implements IButtonMenu {
  readonly title = t('blockQuote.title');
  readonly iconSvg = QUOTE_SVG;
  readonly tag = 'button';
  readonly usableTypes = [
    'paragraph',
    'blockquote',
    'bulleted-list',
    'numbered-list',
  ];

  getValue(editor: IDomEditor): string | boolean {
    // 用不到 getValue
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    const node = DomEditor.getSelectedNodeByType(editor, 'blockquote');
    return !!node;
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) return true;

    const [nodeEntry] = Editor.nodes(editor, {
      match: getMatch,
      universal: true,
      mode: 'highest', // 匹配最高层级
    });

    // 匹配到 p blockquote ，不禁用
    if (nodeEntry) {
      return false;
    }
    // 未匹配到，则禁用
    return true;
  }

  /**
   * 执行命令
   * @param editor editor
   * @param value node.type
   */
  exec(editor: IDomEditor, value: string | boolean) {
    if (this.isDisabled(editor)) return;

    const active = this.isActive(editor);
    const newType = active ? 'paragraph' : 'blockquote';
    /**
    
     * 自定义需求：新增可以用于标题、有序/无序列表等，切换到paragraph时，使用paragraph样式
     */
    const style = active ? toBHeaderStyle(editor, 'paragraph') : {};

    // 执行命令
    Transforms.setNodes(
      editor,
      { type: newType, ...style },
      { mode: 'highest' },
    );

    /**
     * TODO：尝试联合wrapNodes与unwrapNodes，引用包裹内容。（plugin中换行逻辑需要修改）
     */
    // if (active) {
    //   Transforms.unwrapNodes(editor, );
    // } else {
    //   Transforms.wrapNodes(editor, { type: 'blockquote', children: [] });
    // }
  }
}

export default BlockquoteMenu;
