/**
 * @description render link elem
 *
 */

import { Element as SlateElement, Range } from 'slate';
import { jsx, VNode } from 'snabbdom';
import { IDomEditor, DomEditor } from '@klein-editor/core';
import { LinkElement } from './custom-types';

/**
 * render link elem
 * @param elemNode slate elem
 * @param children children
 * @param editor editor
 * @returns vnode
 */
function renderLink(
  elemNode: SlateElement,
  children: VNode[] | null,
  editor: IDomEditor,
): VNode {
  let { url, target = '_blank' } = elemNode as LinkElement;
  const vnode = (
    <a
      href={url}
      target={target}
      on={{
        click: () => {
          const regex = /^(http|https):\/\//;
          if (!regex.test(url)) url = 'http://' + url;
          window.open(url, '_blank');
          const hoverbar = DomEditor.getHoverbar(editor);
          if (hoverbar) hoverbar.hideAndClean();
        },
      }}
    >
      {children}
    </a>
  );

  return vnode;
}

const renderLinkConf = {
  type: 'link', // 和 elemNode.type 一致
  renderElem: renderLink,
};

export { renderLinkConf };
