/**
 * @description editor 插件，重写 editor API
 *
 */

import {
  Editor,
  Node,
  Transforms,
  Element,
  Descendant,
  NodeEntry,
} from 'slate';
import { DomEditor, IDomEditor } from '@klein-editor/core';
import isUrl from 'is-url';
import { isMenuDisabled, insertLink } from './helper';

function withLink<T extends IDomEditor>(editor: T): T {
  const { isInline, insertData, normalizeNode, insertNode, insertText } =
    editor;
  const newEditor = editor;

  // 重写 isInline
  newEditor.isInline = (elem) => {
    const { type } = elem;

    if (type === 'link') {
      return true;
    }

    return isInline(elem);
  };

  // 重写 insertData ，粘贴插入链接
  newEditor.insertData = (data: DataTransfer) => {
    const text = data.getData('text/plain');
    if (!isUrl(text)) {
      // 非链接
      insertData(data);
      return;
    }

    // 插入链接
    if (isMenuDisabled(newEditor)) return; // disabled
    const { selection } = newEditor;
    if (selection == null) return;
    const selectedText = Editor.string(newEditor, selection); // 获取选中的文字
    insertLink(newEditor, selectedText, text);
  };

  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node);
    if (type !== 'link') {
      // 未命中 link ，执行默认的 normalizeNode
      return normalizeNode([node, path]);
    }

    // 如果链接内容为空，则删除
    const str = Node.string(node);

    if (str === '') {
      const [nodeEntry]: any = Node.nodes(node);
      if (!(nodeEntry && nodeEntry[0]))
        return Transforms.removeNodes(newEditor, { at: path });
      if (!Array.isArray(nodeEntry[0].children)) return;
      if (!nodeEntry[0].children.find((child) => Element.isElement(child))) {
        return Transforms.removeNodes(newEditor, { at: path });
      }
    }
    return normalizeNode([node, path]);
  };

  // /**
  //  * 自定义需求：点击链接跳转
  //  */
  // setTimeout(() => {
  //   const textarea = DomEditor.getTextarea(newEditor);
  //   const $container = textarea?.$textAreaContainer;
  //   console.log("🚀 ~ file: plugin.ts ~ line 66 ~ setTimeout ~ $container", $container)
  //   if ($container) {
  //     $container.on('click', (e) => {
  //       // window.open(value, '_blank');
  //       console.log('🚀 ~ file: plugin.ts ~ line 73 ~ e', e);
  //     });
  //   }
  // });

  // 返回 editor ，重要！
  return newEditor;
}

export default withLink;
