/**
 * @description parse style html
 *
 */

import { Descendant, Element } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import $, { DOMElement, getStyles } from '../../utils/dom';
import { DIVElement } from './custom-types';

export function parseStyleHtml(
  elem: DOMElement,
  node: Descendant,
  editor: IDomEditor,
): Descendant {
  const $elem = $(elem);

  const elemNode = node as DIVElement;

  const style = getStyles($elem);
  if (style?.whiteSpace === 'normal') {
    delete style.whiteSpace;
  }
  elemNode.style = style;
  return elemNode;
}
