/**
 * @description editor 插件，重写 editor API
 *
 */

import { Editor, Transforms, Element, Range } from 'slate';
import { DomEditor, IDomEditor } from '@klein-editor/core';
import {
  getMatchNode,
  FORMAT_BRUSH_STYLE_MARKS,
  FORMAT_BRUSH_TEXT_MARKS,
  FORMAT_BRUSH_NODE_TYPE,
} from './helper';

function withFormatBrush<T extends IDomEditor>(editor: T): T {
  const newEditor = editor;

  newEditor.on('selectionchange', function () {
    const { selection, formatBrushStatus, formatBrushStyle } = editor;
    if (selection && formatBrushStatus > 0 && formatBrushStyle) {
      setTimeout(() => {
        const { type: nodeType, ...style } = formatBrushStyle || {};
        const { fontSize, ...extendFormatBrushStyle } = style || {};
        const node = getMatchNode(editor) || ({} as any);
        const marks = Editor.marks(editor) as any;
        const markStyle = Object.assign({}, marks.style || {});
        FORMAT_BRUSH_TEXT_MARKS.map((mark) => {
          if (extendFormatBrushStyle?.[mark]) {
            editor.addMark(mark, extendFormatBrushStyle?.[mark]);
          }
        });
        if (
          FORMAT_BRUSH_NODE_TYPE.includes(nodeType) &&
          FORMAT_BRUSH_NODE_TYPE.includes(node.type)
        ) {
          Transforms.setNodes(
            editor,
            {
              type: nodeType,
              style: extendFormatBrushStyle,
            },
            {
              match: (n) => Element.isElement(n),
              mode: 'lowest',
            },
          );
          editor.addMark('style', { fontSize });
        } else {
          FORMAT_BRUSH_STYLE_MARKS.map((mark) => {
            if (extendFormatBrushStyle?.[mark]) {
              markStyle[mark] = extendFormatBrushStyle[mark];
            }
          });
          editor.addMark('style', { ...markStyle, fontSize });
        }
      }, 1500);
    }
  });

  return newEditor;
}

export default withFormatBrush;
