/**
 * @description render indent style
 *
 */

import { Element, Descendant } from 'slate';
import { jsx, VNode } from 'snabbdom';
import { addVnodeStyle, setVnodeStyle } from '../../utils/vdom';
import { ParagraphElement } from './custom-types';
// import { EXCLUDE_TYPES } from './helper';

/**
 * 添加样式
 * @param node slate elem
 * @param vnode vnode
 * @returns vnode
 */
export function renderStyle(node: Descendant, vnode: VNode): VNode {
  if (!(Element.isElement(node) && node.type === 'paragraph')) return vnode;
  // const isExcludeType = EXCLUDE_TYPES.includes(node.type);
  // if (isExcludeType) return vnode;
  let styleVnode: VNode = vnode;
  const { lineHeight, fontWeight, margin, fontSize, color } =
    node as ParagraphElement;
  if (lineHeight) {
    addVnodeStyle(styleVnode, {
      lineHeight: lineHeight,
    });
  }
  if (fontWeight) {
    addVnodeStyle(styleVnode, {
      fontWeight: fontWeight,
    });
  }
  if (margin) {
    addVnodeStyle(styleVnode, {
      margin: margin,
    });
  }
  if (fontSize) {
    addVnodeStyle(styleVnode, {
      fontSize: fontSize,
    });
  }
  if (color) {
    addVnodeStyle(styleVnode, {
      color: color,
    });
  }
  return styleVnode;
}
