/**
 * @description redo menu
 *
 */

import { IButtonMenu, IDomEditor, t } from '@klein-editor/core';
import { REDO_SVG } from '../../../constants/icon-svg';

class RedoMenu implements IButtonMenu {
  title = t('undo.redo');
  iconSvg = REDO_SVG;
  tag = 'button';
  key = 'redo';

  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    return false;
  }

  isDisabled(editor: IDomEditor): boolean {
    // 自定义需求：无撤销记录，disabled菜单
    const { redos } = editor.history || {};
    if (redos && redos.length > 0) {
      return false;
    } else {
      return true;
    }

    // if (editor.selection == null) return true;
    // return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    if (typeof editor.redo === 'function') {
      editor.redo();
    }
  }
}

export default RedoMenu;
