/**
 * @description parse html
 *
 */

import { Descendant, Text } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import { DIVElement } from './custom-types';
import $, { DOMElement } from '../../utils/dom';

function parseHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): DIVElement {
  const $elem = $(elem);
  //去除粘贴时包的空格行
  children = children.filter((child: any) => {
    if (child.type === 'paragraph' && child.children[0].text === '')
      return false;
    if (
      child.type === 'div:not([data-k-e-type])' &&
      child.children[0].text === ''
    )
      return false;
    return true;
  });
  // 无 children ，则用纯文本
  if (children.length === 0) {
    children = [{ text: $elem.text().replace(/\s+/gm, ' ') }];
  }
  console.log(children);
  editor.customPasteChildren = children;
  return {
    type: 'custom-paste',
    // @ts-ignore
    children: children.map((child: any) => {
      if (child.style?.whiteSpace) delete child.style.whiteSpace;
      return child;
    }),
  };
}

export const parseHtmlConf = {
  selector: 'div[data-k-e-type="custom-paste"]',
  parseElemHtml: parseHtml,
};
