/**
 * @description parse html
 *
 */

import { Descendant, Text } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import { DIVElement } from './custom-types';
import $, { DOMElement } from '../../utils/dom';

function parseDIVHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): DIVElement {
  const $elem = $(elem);

  // 无 children ，则用纯文本
  if (children.length === 0) {
    children = [{ text: $elem.text().replace(/\s+/gm, ' ') }];
  }

  return {
    type: 'div:not([data-k-e-type])',
    // @ts-ignore
    children,
  };
}

export const parseDIVHtmlConf = {
  selector: 'div:not([data-k-e-type])',
  parseElemHtml: parseDIVHtml,
};
