/**
 * @description header menu
 *
 */

import { Editor, Node, Element, Transforms } from 'slate';
import { ISelectMenu, IDomEditor, IOption, t } from '@klein-editor/core';
import { LINE_HEIGHT_SVG } from '../../../constants/icon-svg';
import { LineHeightElement } from '../custom-types';

class LineHeightMenu implements ISelectMenu {
  readonly title = t('lineHeight.title');
  readonly iconSvg = LINE_HEIGHT_SVG;
  readonly tag = 'select';
  readonly wide = true;
  // readonly width = 80;

  getOptions(editor: IDomEditor): IOption[] {
    const options: IOption[] = [];

    // 获取配置，参考 './config.ts'
    const { lineHeightList = [] } = editor.getMenuConfig('lineHeight');

    // 生成 options
    options.push({
      text: '自动',
      value: 'normal', // this.getValue(editor) 未找到结果时，会返回 '' ，正好对应到这里
    });
    lineHeightList.forEach((height: string) => {
      options.push({
        text: height,
        value: height,
      });
    });

    // 设置 selected
    const curValue = this.getValue2(editor);
    options.forEach((opt) => {
      if (opt.value === curValue) {
        opt.selected = true;
      } else {
        delete opt.selected;
      }
    });

    return options;
  }

  /**
   * 获取匹配的 node 节点
   * @param editor editor
   */
  private getMatchNode(editor: IDomEditor): Node | null {
    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => Element.isElement(n),
      universal: true,
      // mode: 'highest', // 匹配最高层级
      mode: 'lowest',
    });

    if (nodeEntry == null) return null;
    return nodeEntry[0];
  }

  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }

  /**   *
   * 菜单不回显值，所以传false
   */
  getValue(editor: IDomEditor): string | boolean {
    return false;
  }

  /**
   * 获取 node.lineHeight 的值（如 '1' '1.5'），没有则返回 ''
   * @param editor editor
   *
/**
   * 获取 node.lineHeight 的值（如 '1' '1.5'），没有则返回 ''
   * @param editor editor
   * 
   * 选项需要回显，所以获取具体值
   */
  getValue2(editor: IDomEditor): string | boolean {
    const node = this.getMatchNode(editor);
    if (node == null) return false;

    const { lineHeightList = [], lineHeight: defaultLineHeight } =
      editor.getMenuConfig('lineHeight');
    const lineHeight =
      (node as LineHeightElement).style?.lineHeight || defaultLineHeight;
    return lineHeightList.includes(lineHeight) ? lineHeight : defaultLineHeight;
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) return true; // 禁用

    const node = this.getMatchNode(editor);
    if (node == null) return true; // 未匹配到指定 node ，禁用

    return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    /**
     * 自定义需求：将设置最顶层节点lineHeight，改为上级第一个Element节点；
     */

    const node = this.getMatchNode(editor);
    const style = (node as any)?.style || {};
    console.log(style, value);
    Transforms.setNodes(
      editor,
      {
        style: {
          ...style,
          lineHeight: value.toString(),
        },
      },
      {
        match: (n) => Element.isElement(n),
        mode: 'lowest',
      },
    );
  }
}

export default LineHeightMenu;
