/**
 * @description textStyle to html
 *
 */

import { Element, Descendant } from 'slate';
import $, { getOuterHTML } from '../../utils/dom';
import { toLine } from '../../utils/util';
import { DIVElement } from './custom-types';

export function styleToHtml(node: Descendant, html: string): string {
  const { style } = node as DIVElement;

  const formatStyle = {};
  if (style) {
    Object.keys(style).forEach((key) => {
      formatStyle[toLine(key)] = style[key];
    });
  }

  // 设置样式
  let $elem;
  if (Element.isElement(node)) {
    $elem = $(html);
  } else {
    $elem = $(`<span>${html}</span>`);
  }
  $elem.css(formatStyle);

  // 输出 html
  return getOuterHTML($elem);
}
