/**
 * @description blockquote helper
 *
 */

import { DomEditor } from '@klein-editor/core';

export const getMatch = (n) => {
  const type = DomEditor.getNodeType(n);

  // // 只可用于 p 和 blockquote
  // if (type === 'paragraph') return true;
  // if (type === 'blockquote') return true;
  /**
   * 自定义需求：可用于标题、有序/无序列表、段落
   */
  if (type.startsWith('header')) return true;
  if (['paragraph', 'blockquote', 'bulleted-list', 'numbered-list'])
    return true;

  return false;
};
