/**
 * @description header menu
 *
 */

import { Editor, Node, Element, Transforms } from 'slate';
import { ISelectMenu, IDomEditor, IOption, t } from '@klein-editor/core';
import { MARGIN_BOTTOM_SVG } from '../../../constants/icon-svg';
import { MarginElement } from '../custom-types';

class MarginBottomMenu implements ISelectMenu {
  readonly title = t('margin.bottom');
  readonly iconSvg = MARGIN_BOTTOM_SVG;
  readonly tag = 'select';
  readonly wide = true;

  getOptions(editor: IDomEditor): IOption[] {
    const options: IOption[] = [];

    const { defaultValue, marginList = [] } =
      editor.getMenuConfig('marginBottom');

    const curValue = this.getValue2(editor);

    marginList.forEach((value: string) => {
      const selected = value === curValue;
      const text = `${value}${value === defaultValue ? '（默认）' : ''}`;
      options.push({
        text,
        value,
        selected,
      });
    });

    return options;
  }

  /**
   * 获取匹配的 node 节点
   * @param editor editor
   */
  private getMatchNode(editor: IDomEditor): Node | null {
    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => Element.isElement(n),
      universal: true,
      mode: 'highest',
    });

    if (nodeEntry == null) return null;
    return nodeEntry[0];
  }

  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }

  /**
   * 选项需要回显，所以获取具体值
   */
  getValue2(editor: IDomEditor): string | boolean {
    const node = this.getMatchNode(editor);
    if (node == null) return false;

    const { marginList = [] } = editor.getMenuConfig('marginBottom');
    const nodeStye = (node as MarginElement).style || {};
    const margin = nodeStye.margin || (node as any)?.margin;
    const { marginBottom } = nodeStye;

    let value = '0';
    if (margin) {
      const values = margin.split(' ');
      switch (values.length) {
        case 1:
        case 2: {
          value = `${parseInt(values[0])}`;
          break;
        }
        case 3:
        case 4: {
          value = `${parseInt(values[2])}`;
          break;
        }
      }
    }
    if (marginBottom) value = `${parseInt(marginBottom)}`;

    return marginList.includes(value) ? value : '';
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) return true;

    const node = this.getMatchNode(editor);
    if (node == null) return true;

    return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    const node = this.getMatchNode(editor);
    const style = (node as any)?.style || {};
    const margin = style.margin || (node as any)?.margin;
    const newStyle: any = {};
    const newValue = `${value}px`;
    if (margin) {
      const values = margin.split(' ');
      switch (values.length) {
        case 1: {
          const oldValue = values[0];
          values[0] = newValue;
          values.unshift(oldValue, oldValue);
          break;
        }
        case 2: {
          values.push(newValue);
          break;
        }
        case 3:
        case 4: {
          values.splice(2, 1, newValue);
          break;
        }
      }
      newStyle.margin = values.join(' ');
    } else {
      newStyle.marginBottom = newValue;
    }

    const nodeStyle = {
      ...style,
      ...newStyle,
    };

    Transforms.setNodes(
      editor,
      {
        style: nodeStyle,
      },
      {
        match: (n) => Element.isElement(n),
        mode: 'highest',
      },
    );
  }
}

export default MarginBottomMenu;
