/**
 * @description editor 插件，重写 editor API
 *
 */

import { Path, Node, Transforms, Element, Descendant } from 'slate';
import { DomEditor, IDomEditor } from '@klein-editor/core';
import { CustomElement } from 'packages/custom-types';

function withImage<T extends IDomEditor>(editor: T): T {
  const { isInline, isVoid, normalizeNode } = editor;
  const newEditor = editor;

  // 重写 isInline
  newEditor.isInline = (elem) => {
    const { type } = elem;

    if (type === 'image') {
      return true;
    }

    return isInline(elem);
  };

  // 重写 isVoid
  newEditor.isVoid = (elem) => {
    const { type } = elem;

    if (type === 'image') {
      return true;
    }

    return isVoid(elem);
  };
  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node);
    if (type !== 'image') {
      // 未命中 image ，执行默认的 normalizeNode
      return normalizeNode([node, path]);
    }

    // 如果链接内容为空，则删除
    const imageNode = node as any;
    if (imageNode?.src === '') {
      console.log(imageNode, 'imageNode');
      return Transforms.removeNodes(newEditor, { at: path });
    }
    return normalizeNode([node, path]);
  };

  // 返回 editor ，重要！
  return newEditor;
}

export default withImage;
