/**
 * @description header helper
 *
 */

import { Editor, Transforms, Element, Path, Node, Text } from 'slate';
import { IDomEditor, DomEditor, IOption } from '@klein-editor/core';
import { HeaderStyleMap, HeaderType } from './custom-types';

export function toBHeaderStyle(editor, tag?: string) {
  const {
    MENU_CONF: {
      lineHeight: { lineHeight },
      fontSize: { fontSize },
      color: { color },
    },
  } = editor.getConfig();
  const toBHeaderStyle: HeaderStyleMap = {
    paragraph: {
      fontSize: fontSize || '16px',
      lineHeight: lineHeight || 1.75,
      margin: '0px 0px 8px',
      fontWeight: '400',
      color: color,
    },
    header1: {
      fontSize: '36px',
      lineHeight: '50px',
      margin: '48px 0 28px 0',
      fontWeight: '600',
    },
    header2: {
      fontSize: '32px',
      lineHeight: '45px',
      margin: '44px 0 24px 0',
      fontWeight: '600',
    },
    header3: {
      fontSize: '28px',
      lineHeight: '40px',
      margin: '40px 0 20px 0',
      fontWeight: '600',
    },
    header4: {
      fontSize: '24px',
      lineHeight: '34px',
      margin: '36px 0 16px 0',
      fontWeight: '600',
    },
    header5: {
      fontSize: '20px',
      lineHeight: '28px',
      margin: '32px 0 12px 0',
      fontWeight: '600',
    },
    header6: {
      fontSize: '16px',
      lineHeight: '22px',
      margin: '24px 0 8px 0',
      fontWeight: '600',
    },
  };
  if (!tag) return toBHeaderStyle;
  return toBHeaderStyle[tag];
}

export function toCHeaderStyle(editor, tag?: string) {
  const {
    MENU_CONF: {
      lineHeight: { lineHeight },
      fontSize: { fontSize },
      color: { color },
    },
  } = editor.getConfig();
  const toCHeaderStyle: HeaderStyleMap = {
    paragraph: {
      fontSize: fontSize || '16px',
      lineHeight: lineHeight || 1.75,
      margin: '0px 0px 8px',
      fontWeight: 'normal',
      color: color,
    },
    header1: {
      fontSize: '32px',
      lineHeight: '45px',
      margin: '44px 0 24px 0',
      fontWeight: '600',
    },
    header2: {
      fontSize: '24px',
      lineHeight: '34px',
      margin: '36px 0 16px 0',
      fontWeight: '600',
    },
    header3: {
      fontSize: '18px',
      lineHeight: '25px',
      margin: '28px 0 10px 0',
      fontWeight: '600',
    },
    header4: {
      fontSize: '16px',
      lineHeight: '22px',
      margin: '24px 0 8px 0',
      fontWeight: '600',
    },
    header5: {
      fontSize: '14px',
      lineHeight: '20px',
      margin: '20px 0 8px 0',
      fontWeight: '600',
    },
  };
  if (!tag) return toCHeaderStyle;
  return toCHeaderStyle[tag];
}

export function getCurrentStyle(editor: IDomEditor, tag: HeaderType) {
  return toBHeaderStyle(editor, tag);
}

/**
 * 获取匹配的 node 节点
 * @param editor editor
 */
export function getMatchNode(editor: IDomEditor): Node | null {
  const [nodeEntry] = Editor.nodes(editor, {
    match: (n) => {
      // 只可用于header及paragraph
      const type = DomEditor.getNodeType(n);
      const isMatchType = type.startsWith('header') || type === 'paragraph';
      return isMatchType;
    },
    universal: true,
    mode: 'lowest', // 匹配最低层级
  });

  if (!nodeEntry?.length) return null;

  return nodeEntry[0];
}

/**
 * 获取 node type（'header1' 'header2' 等），未匹配则返回 'paragraph'
 */
export function getHeaderType(editor: IDomEditor): string {
  const node = getMatchNode(editor);

  // 未匹配到 header
  if (node == null) return 'paragraph';

  return DomEditor.getNodeType(node);
}

export function isMenuDisabled(editor: IDomEditor): boolean {
  if (editor.selection == null) return true;

  const node = getMatchNode(editor);

  // 匹配到 p header ，不禁用
  if (node) {
    return false;
  }
  // 未匹配到 p header ，则禁用
  return true;
}

/**
 * 设置 node type （'header1' 'header2' 'paragraph' 等）
 */
export function setHeaderType(editor: IDomEditor, type: string) {
  if (!type) return;

  const [nodeEntry] = Editor.nodes(editor, {
    match: (n) => Element.isElement(n),
    mode: 'lowest', // 匹配最低层级
  });
  if (!nodeEntry?.length) return;

  const [node, path] = nodeEntry;
  const isFirstChild = path.join('').replace(/0/g, '') === '';
  const nodeStyle = (node as any).style || {};

  const { h5Mode } = editor.getConfig();
  const styles = h5Mode
    ? toCHeaderStyle(editor, type)
    : toBHeaderStyle(editor, type);
  // 第一个元素不能有marginTop，在core renderelement时还需处理
  const noMarginTopStyles = {
    ...styles,
  };
  noMarginTopStyles.margin = noMarginTopStyles.margin?.replace(/^(\d+px)/, '0');

  Transforms.setNodes(
    editor,
    {
      type,
      style: {
        ...nodeStyle,
        ...(isFirstChild ? noMarginTopStyles : styles),
      },
    },
    {
      match: (n) => Element.isElement(n),
      mode: 'lowest',
    },
  );
  Transforms.setNodes(
    editor,
    {
      type,
      style: {},
    },
    {
      match: (n) => Text.isText(n),
      mode: 'lowest',
    },
  );
}

/**
 * 根据h5Mode获取options
 */
export function getHeaderOptions(editor: IDomEditor) {
  const { h5Mode } = editor.getConfig();
  const options: IOption[] = [];
  try {
    const headerStyle = h5Mode
      ? toCHeaderStyle(editor)
      : toBHeaderStyle(editor);
    Object.keys(headerStyle).forEach((key) => {
      const style = { ...headerStyle[key] };
      delete style.margin;
      options.push({
        value: key,
        text: key === 'paragraph' ? '正文' : `标题${key.replace('header', '')}`,
        styleForRenderMenuList: style,
      });
    });
    // 插入分隔线
    if (options.length) {
      options.splice(1, 0, {
        value: 'line',
        text: '',
        line: true,
      });
    }
  } catch (error) {}
  return options;
}
