/**
 * @description header menu
 *
 */

import { ISelectMenu, IDomEditor, IOption, t } from '@klein-editor/core';
import { INDENT_LEFT_SVG, INDENT_RIGHT_SVG } from '../../../constants/icon-svg';
import { setIndentType } from '../helper';

class IndentSelectMenu implements ISelectMenu {
  readonly title = t('indentSelect.title');
  readonly iconSvg = INDENT_RIGHT_SVG;
  readonly tag = 'select';
  // readonly width = 36;
  readonly selectPanelWidth = 88;
  readonly wide = true;
  readonly setValue = true;

  getOptions(editor: IDomEditor): IOption[] {
    // 基本的 options 列表
    const options = [
      // value 和 elemNode.type 对应
      {
        value: 'increase_indent',
        text: INDENT_RIGHT_SVG,
        title: '增加缩进',
        textIsTag: true,
        showSelectedIcon: false,
      },
      {
        value: 'decrease_indent',
        text: INDENT_LEFT_SVG,
        title: '减少缩进',
        textIsTag: true,
        showSelectedIcon: false,
      },
    ];

    const curValue = this.getValue(editor).toString();
    options.forEach((opt: IOption) => {
      opt.styleForRenderMenuList = { 'font-size': '0', padding: '8px 36px' };
      if (opt.value === curValue) {
        opt.selected = true;
      } else {
        delete opt.selected;
      }
    });

    return options;
  }

  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }

  /**
   * 获取选中节点的 node.type
   * @param editor editor
   */
  getValue(editor: IDomEditor): string | boolean {
    // return getIndentType(editor);
    return '';
  }

  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  /**
   * 执行命令
   * @param editor editor
   * @param value node.type
   */
  exec(editor: IDomEditor, value: string | boolean) {
    //【注意】value 是 select change 时获取的，并不是 this.getValue 的值
    setIndentType(editor, value.toString());
  }
}

export default IndentSelectMenu;
