/**
 * @description font-size menu
 *
 */

import { Editor, Element, Node } from 'slate';
import {
  ISelectMenu,
  IDomEditor,
  DomEditor,
  IOption,
  t,
} from '@klein-editor/core';
import { getCurrentStyle } from '../helper';
// import { FONT_SIZE_SVG } from '../../../constants/icon-svg';

class FontSizeMenu implements ISelectMenu {
  readonly title = t('fontSize.title');
  // readonly iconSvg = FONT_SIZE_SVG;
  key = 'fontSize';
  readonly wide?: boolean;
  readonly tag = 'select';
  readonly setValue = true;
  readonly width = 58;

  getOptions(editor: IDomEditor): IOption[] {
    const options: IOption[] = [
      {
        text: '-',
        value: '-',
        styleForRenderMenuList: { padding: '5px 36px' },
      },
    ];
    // 获取配置，参考 './config.ts'
    const { fontSizeList = [] } = editor.getMenuConfig(this.key);

    // 生成 options
    fontSizeList.forEach((size: string) => {
      options.push({
        text: size,
        value: size,
        styleForRenderMenuList: { padding: '5px 36px' },
      });
    });

    // 设置 selected
    const curValue = this.getValue(editor);
    options.forEach((opt) => {
      if (opt.value === curValue) {
        opt.selected = true;
      } else {
        delete opt.selected;
      }
    });

    return options;
  }
  /**
   * 获取匹配的 node 节点
   * @param editor editor
   */
  getMatchNode(editor: IDomEditor): Node | null {
    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => Element.isElement(n),
      universal: true,
      mode: 'lowest',
    });

    if (nodeEntry == null) return null;

    return nodeEntry[0];
  }

  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }
  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) return true;

    // const mark = this.mark;
    const [match] = Editor.nodes(editor, {
      match: (n) => {
        const type = DomEditor.getNodeType(n);
        if (type === 'pre') return true; // 代码块
        if (Element.isElement(n) && Editor.isVoid(editor, n)) return true; // void node

        return false;
      },
      universal: true,
    });

    // 匹配到，则禁用
    if (match) return true;
    return false;
  }
  /**
   * 获取 node.fontSize 的值，默认16px，没有匹配到预设值则返回 ''
   * @param editor editor
   */
  getValue(editor: IDomEditor): string | boolean {
    const mark = this.key;

    const { fontSizeList: list = [], fontSize: defaultFontSize } =
      editor.getMenuConfig(mark);

    // 使用选中文案font-size, 否则查找父节点：段落、标题等
    const curMarks = Editor.marks(editor) as any;
    const fontSize = curMarks?.style?.[mark];
    if (fontSize) return list.includes(fontSize) ? fontSize : fontSize;

    const node = this.getMatchNode(editor);
    if (node) {
      const fontSize = (node as any)?.style?.fontSize || (node as any).fontSize;
      return list.includes(fontSize) ? fontSize : fontSize;
    }

    return defaultFontSize || '16px';
  }
  exec(editor: IDomEditor, value: string | boolean) {
    const mark = this.key;
    if (value) {
      if (mark === 'fontSize') {
        const node = this.getMatchNode(editor);
        const nodeStyle = (node as any)?.style || {};
        const curStyle = getCurrentStyle(value);
        const newStyle = {
          ...nodeStyle,
          ...curStyle,
          fontSize: value,
        };
        // Transforms.setNodes(
        //   editor,
        //   {
        //     style: newStyle,
        //   },
        //   {
        //     match: (n, p) => {
        //       return Text.isText(n);
        //     },
        //     mode: 'lowest',
        //   },
        // );
        editor.addMark('style', { fontSize: value });
      }
    } else {
      editor.removeMark(mark);
    }
  }
}

export default FontSizeMenu;
