/**
 * @description FormatBrush menu
 *
 */

import { DomEditor, IButtonMenu, IDomEditor, t } from '@klein-editor/core';
import { FORMAT_BRUSH_SVG } from '../../../constants/icon-svg';
import { getMatchStyles } from '../helper';
import $ from '../../../utils/dom';

class FormatBrush implements IButtonMenu {
  title = t('formatBrush.title');
  activeTitle = t('formatBrush.activeTip');
  iconSvg = FORMAT_BRUSH_SVG;
  tag = 'button';

  timer = setTimeout(() => {}, 0);
  delay = 200;
  startTime = new Date().getTime();

  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    if (editor.formatBrushStatus && editor.formatBrushStatus > 0) {
      $('.k-e-text-container').css(
        'cursor',
        `url(https://image-c.weimobwmc.com/sass-admin/39dcf9b9eac44a8382ba17b78f69069c.svg), text`,
      );
    } else {
      $('.k-e-text-container').css('cursor', ``);
    }
    return (
      typeof editor.formatBrushStatus === 'number' &&
      editor.formatBrushStatus > 0
    );
  }

  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    const currTime = new Date().getTime();
    if (currTime - this.startTime < this.delay) {
      clearTimeout(this.timer);
      const style = getMatchStyles(editor);
      editor.formatBrush(2, style);
    } else {
      this.timer = setTimeout(() => {
        const style = getMatchStyles(editor);
        editor.formatBrush(1, style);
      }, this.delay);
    }
    this.startTime = currTime;
    const toolbar = DomEditor.getToolbar(editor);
    toolbar && toolbar.changeToolbarState();
  }
}

export default FormatBrush;
