/**
 * @description parse style html
 *
 */

import { Descendant, Element } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import $, { DOMElement, getStyleValue } from '../../utils/dom';
import { LinkElement } from './custom-types';

export function parseStyleHtml(
  elem: DOMElement,
  node: Descendant,
  editor: IDomEditor,
): Descendant {
  const $elem = $(elem);
  if (!(Element.isElement(node) && node.type === 'link')) return node;
  const { linkColor } = editor.getMenuConfig('insertLink');
  const elemNode = node as LinkElement;
  const elemNodeStyle = elemNode?.style || {};
  const color = getStyleValue($elem, 'color');
  if (!color) {
    elemNodeStyle.color = linkColor || '';
  }
  elemNode.style = elemNodeStyle;
  return elemNode;
}
