/**
 * @description editor 插件，重写 editor API
 *
 */

import {
  Editor,
  Element,
  Transforms,
  Node as SlateNode,
  Text as SlateText,
} from 'slate';
import { DomEditor, IDomEditor } from '@klein-editor/core';

function deleteHandler(newEditor: IDomEditor): boolean {
  const [nodeEntry] = Editor.nodes(newEditor, {
    match: (n) => newEditor.children[0] === n, // editor 第一个节点
    mode: 'highest', // 最高层级
  });
  if (nodeEntry == null) return false;

  const n = nodeEntry[0];

  if (!Element.isElement(n)) return false;
  if (n.type === 'paragraph') {
    /**
     * 自定义需求：第一个节点为空paragraph，且还有其他节点，支持删除
     */
    if (SlateNode.string(n) === '' && newEditor.children.length > 1) {
      Transforms.removeNodes(newEditor, { mode: 'highest' });
      return true;
    }
    return false;
  } // 命中了 paragraph ，则不再继续判断
  if (SlateNode.string(n) !== '') return false; // 未删除全部内容，则不再继续判断

  const { children = [] } = n;
  if (!SlateText.isText(children[0])) return false; // n.children 不是 text （如 table），则不再继续判断

  // 至此，就命中了一个（非 paragraph）+（children 都是 text）+（内容为空）的顶级 node ，如 header blockQuote 等
  // 然后，将其却换为 paragraph
  /**
   * 自定义需求：预设段落样式
   */
  const p = DomEditor.genEmptyParagraph(newEditor);
  Transforms.setNodes(newEditor, p, { mode: 'highest' });
  return true;
}
function deleteParagraphHandler(newEditor: IDomEditor): boolean {
  const [nodeEntry] = Editor.nodes(newEditor, {
    match: (n) => Element.isElement(n),
    mode: 'highest', // 最高层级
  });
  if (nodeEntry == null) return false;
  const [node, path] = nodeEntry;
  // 获取相邻的上一个节点
  const previousNodeEntry = Editor.previous(newEditor, { at: path });
  if (previousNodeEntry == null) return false;
  if (!Element.isElement(node)) return false;
  if (node.type === 'paragraph' && SlateNode.string(node) === '') {
    Transforms.removeNodes(newEditor, { mode: 'highest' });
    return true;
  }
  return false;
}
function withParagraph<T extends IDomEditor>(editor: T): T {
  const { deleteBackward, deleteForward, insertText, insertBreak } = editor;
  const newEditor = editor;

  // 删除非 p 的文本 elem（如 header blockQuote 等），删除没有内容时，切换为 p
  newEditor.deleteBackward = (unit) => {
    const res = deleteHandler(newEditor);
    if (res) return; // 命中结果，则 return
    const prevRes = deleteParagraphHandler(newEditor);
    if (prevRes) return;

    // 执行默认的删除
    deleteBackward(unit);
  };
  newEditor.deleteForward = (unit) => {
    const res = deleteHandler(newEditor);
    if (res) return; // 命中结果，则 return

    // 执行默认的删除
    deleteForward(unit);
  };
  // 重写 insertBreak - paragraph末尾回车时要插入 paragraph
  // newEditor.insertBreak = () => {
  //   const [match] = Editor.nodes(newEditor, {
  //     match: (n) => DomEditor.checkNodeType(n, 'paragraph'),
  //     universal: true,
  //   });

  //   if (!match) {
  //     // 未匹配到
  //     insertBreak();
  //     return;
  //   }

  //   const isAtLineEnd = DomEditor.isSelectionAtLineEnd(editor, match[1]);

  //   // 如果在行末插入一个空 p，否则正常换行
  //   if (isAtLineEnd) {
  //     const p = DomEditor.genEmptyParagraph(editor);
  //     Transforms.insertNodes(newEditor, p, { mode: 'highest' });
  //   } else {
  //     insertBreak();
  //   }
  // };

  // 返回 editor ，重要！
  return newEditor;
}

export default withParagraph;
