/**
 * @description header helper
 *
 */

import { Editor, Transforms, Element } from 'slate';
import { IDomEditor, DomEditor } from '@klein-editor/core';

const span = 28;
const maxSpan = 196;

export function getIndent(editor: IDomEditor): string {
  const [match] = Editor.nodes(editor, {
    match: (n) => {
      const { indent } = n as any;
      return indent;
    },
    universal: true,
  });

  if (!match) return '';
  const [n] = match;
  return (n as any).indent;
}

// export function getIndentType(editor: IDomEditor): string {
//   const indent = getIndent(editor)

//   return indent == '2em' ? 'increase_indent' : 'decrease_indent';
// }

export function setIndentType(editor: IDomEditor, type: string) {
  if (!type) return;

  const currentIndent = getIndent(editor);
  let nextIndent: string = '';

  const indentNum = parseInt(currentIndent.split('px')[0]) || 0;
  if (type === 'increase_indent') {
    const nextIndentNum = indentNum + span;
    if (nextIndentNum > maxSpan) return;
    nextIndent = `${nextIndentNum}px`;
  } else {
    nextIndent = indentNum === 0 ? '0px' : `${indentNum - span}px`;
  }

  // 执行命令
  Transforms.setNodes(
    editor,
    {
      indent: nextIndent,
    },
    {
      match: (n) => Element.isElement(n),
      mode: 'highest',
    },
  );
}
