import {
  ISelectMenu,
  IDomEditor,
  DomEditor,
  IOption,
  t,
} from '@klein-editor/core';
import { Editor, Transforms, Node, Element } from 'slate';
import { toBHeaderStyle, toCHeaderStyle } from '../../header/helper';
import { FORMAT_SVG } from '../../../constants/icon-svg';

const formatOption = {};
const fillterTag = ['image', 'link'];
class FormatSelectMenu implements ISelectMenu {
  readonly title = t('');
  readonly iconSvg = FORMAT_SVG;
  readonly tag = 'select';
  readonly width = 36;
  readonly selectPanelWidth = 108;
  readonly wide = true;
  readonly setValue = true;
  readonly key = 'saveCustomPasteFormat';
  getOptions(editor: IDomEditor): IOption[] {
    const { customPasteOptionList } = editor.getMenuConfig(this.key);

    const curValue = this.getValue(editor).toString();
    customPasteOptionList.forEach((opt: IOption) => {
      opt.styleForRenderMenuList = {
        'font-size': '14px',
        padding: '8px 0 8px 12px',
      };
      if (opt.value === curValue) {
        opt.selected = true;
      } else {
        delete opt.selected;
      }
    });
    return customPasteOptionList;
  }

  private recursionChildren(editor, children) {
    if (Array.isArray(children)) {
      return children.map((item) => {
        const {
          type,
          style = {},
          text = '',
          children: itemChildren = [],
        } = item;
        if (typeof item === 'object' && item !== null) {
          const hasStyle = Object.prototype.hasOwnProperty.call(item, 'style');
          const { h5Mode } = editor.getConfig();
          const styles = h5Mode
            ? toCHeaderStyle(editor, type) || {}
            : toBHeaderStyle(editor, type) || {};
          const newStyle = hasStyle ? styles : item.style;
          const newChildren = this.recursionChildren(editor, item.children);
          if (fillterTag.includes(type))
            return { ...item, children: newChildren };
          if (type) return { type, style: newStyle, children: newChildren };
          return { text, style: newStyle, children: newChildren };
        } else {
          if (type) return { type, text, style, children: itemChildren };
          return { text, style, children: itemChildren };
        }
      });
    } else {
      return children;
    }
  }
  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }

  /**
   * 获取选中节点的 node.type
   * @param editor editor
   */
  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  /**
   * 执行命令
   * @param editor editor
   * @param value node.type
   */
  exec(editor: IDomEditor, value: string | boolean) {
    // 【注意】value 是 select change 时获取的，并不是 this.getValue 的值
    if (this.isDisabled(editor)) return;
    const { customPasteOptionList } = editor.getMenuConfig(this.key);
    const p = DomEditor.genEmptyParagraph(editor);
    const initialEditorValue: Node[] = [p];
    if (value === customPasteOptionList[0].value) {
      // 保留源格式
      const selectedCustomPasteNode = DomEditor.getSelectedNodeByType(
        editor,
        'custom-paste',
      );
      if (selectedCustomPasteNode == null) return;

      Transforms.removeNodes(editor, {
        match: (n) => DomEditor.checkNodeType(n, 'custom-paste'),
      });
      if (editor.children.length === 0) {
        Transforms.insertNodes(editor, initialEditorValue);
      }
      editor.dangerouslyInsertHtml(editor.customPasteHtml);
    } else if (value === customPasteOptionList[1].value) {
      // 保留目标格式 编辑器默认格式
      const selectedCustomPasteNode = DomEditor.getSelectedNodeByType(
        editor,
        'custom-paste',
      );
      if (!selectedCustomPasteNode) return;
      const { type } = selectedCustomPasteNode as any;
      const newChildren = this.recursionChildren(
        editor,
        editor.customPasteChildren,
      );
      const props = {
        type,
        style: {},
        children: newChildren,
      };
      Editor.withoutNormalizing(editor, () => {
        Transforms.removeNodes(editor, {
          match: (n) => DomEditor.checkNodeType(n, type),
        });
        Transforms.insertNodes(editor, props);
      });
    } else if (value === customPasteOptionList[2].value) {
      const selectedCustomPasteNode = DomEditor.getSelectedNodeByType(
        editor,
        'custom-paste',
      );
      const { type } = selectedCustomPasteNode as any;
      if (selectedCustomPasteNode == null) return;
      const props = {
        type,
        children: [{ text: editor.customPasteText }],
      };
      Editor.withoutNormalizing(editor, () => {
        Transforms.removeNodes(editor, {
          match: (n) => DomEditor.checkNodeType(n, type),
        });
        Transforms.insertNodes(editor, props);
      });
    }
  }
}
export default FormatSelectMenu;
