/**
 * @description to html
 *
 */

import { Element } from 'slate';

function sectionToHtml(elem: Element, childrenHtml: string): string {
  if (childrenHtml === '') {
    return '<section><br></section>';
  }
  return `<section>${childrenHtml}</section>`;
}

export const sectionToHtmlConf = {
  type: 'section',
  elemToHtml: sectionToHtml,
};
