/**
 * @description parse html
 *
 */

import { Descendant } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import { BlockElement } from './custom-types';
import $, { DOMElement } from '../../utils/dom';

function parseBlockHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): BlockElement {
  const $elem = $(elem);

  // 无 children ，则用纯文本
  if (children.length === 0) {
    children = [{ text: $elem.text().replace(/\s+/gm, ' ') }];
  }

  return {
    type: 'block',
    // @ts-ignore
    children,
  };
}

export const parseBlockHtmlConf = {
  selector: '[data-k-e-type="block"]',
  parseElemHtml: parseBlockHtml,
};
