/**
 * @description parse html
 *
 */

import { Descendant, Text } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import { SectionElement } from './custom-types';
import $, { DOMElement } from '../../utils/dom';

function parseSectionHtml(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): SectionElement {
  const $elem = $(elem);
  // 无 children ，则用纯文本
  if (children.length === 0) {
    children = [{ text: $elem.text().replace(/\s+/gm, ' ') }];
  }

  return {
    type: 'section',
    // @ts-ignore
    children,
  };
}

export const parseSectionHtmlConf = {
  selector: 'section,article,aside,footer',
  parseElemHtml: parseSectionHtml,
};
