/**
 * @description textStyle to html
 *
 */

import { Element, Descendant } from 'slate';
import $, { getOuterHTML } from '../../utils/dom';
import { ParagraphElement } from './custom-types';

export function styleToHtml(node: Descendant, elemHtml: string): string {
  if (!(Element.isElement(node) && node.type === 'paragraph')) return elemHtml;

  let defaultHtmlStyle: any = {};
  const { lineHeight, margin, fontSize, fontWeight, color } =
    node as ParagraphElement;
  if (lineHeight) {
    defaultHtmlStyle.lineHeight = lineHeight;
  }
  if (margin) {
    defaultHtmlStyle.margin = margin;
  }
  if (fontSize) {
    defaultHtmlStyle.fontSize = fontSize;
  }
  if (fontWeight) {
    defaultHtmlStyle.fontWeight = fontWeight;
  }
  if (color) {
    defaultHtmlStyle.color = color;
  }
  // 设置样式
  const $elem = $(elemHtml);
  $elem.css(defaultHtmlStyle);

  // 输出 html
  return getOuterHTML($elem);
}
