/**
 * @description line-height menu entry
 *
 */

import MarginTopMenu from './MarginTopMenu';
import MarginBottomMenu from './MarginBottomMenu';
import { getMarginConfig } from './config';

export const marginTopMenuConf = {
  key: 'marginTop',
  factory() {
    return new MarginTopMenu();
  },

  config: {
    defaultValue: '0',
    marginList: getMarginConfig(),
  },
};

export const marginBottomMenuConf = {
  key: 'marginBottom',
  factory() {
    return new MarginBottomMenu();
  },

  config: {
    defaultValue: '8',
    marginList: getMarginConfig(),
  },
};
