/**
 * @description ClearFormat menu
 *
 */

import { Editor, Element, Transforms } from 'slate';
import { IButtonMenu, DomEditor, IDomEditor, t } from '@klein-editor/core';
import { CLEAR_FORMAT_SVG } from '../../../constants/icon-svg';
import { FORMAT_BRUSH_TEXT_MARKS, getMatchNode } from '../helper';
import { toBHeaderStyle, toCHeaderStyle } from '../../header/helper';

class ClearFormat implements IButtonMenu {
  title = t('clearFormat.title');
  iconSvg = CLEAR_FORMAT_SVG;
  tag = 'button';

  getValue(editor: IDomEditor): string | boolean {
    return '';
  }

  isActive(editor: IDomEditor): boolean {
    return false;
  }

  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  exec(editor: IDomEditor, value: string | boolean) {
    FORMAT_BRUSH_TEXT_MARKS.map((mark) => {
      editor.addMark(mark, undefined);
    });
    editor.addMark('style', undefined);

    const [nodeEntry] = Editor.nodes(editor, {
      match: (n) => Element.isElement(n),
      mode: 'highest', // 匹配最高层级
    });
    if (!nodeEntry?.length) return;
    const [node, path] = nodeEntry;
    const isFirstChild = path.join('').replace(/0/g, '') === '';
    const { type: nodeType, style } = node as any;
    const type =
      nodeType.startsWith('header') ||
      nodeType === 'div[data-k-e-type]' ||
      nodeType === 'div:not([data-k-e-type])' ||
      nodeType === 'custom-paste' ||
      nodeType === 'list-item'
        ? 'paragraph'
        : nodeType;
    const nodeStyle = style || {};
    console.log(nodeType, 'typetype');
    const { h5Mode } = editor.getConfig();
    const styles = h5Mode
      ? toCHeaderStyle(editor, type)
      : toBHeaderStyle(editor, type);
    // 第一个元素不能有marginTop，在core renderelement时还需处理
    const noMarginTopStyles = {
      ...(styles || {}),
    };
    noMarginTopStyles.margin = noMarginTopStyles.margin?.replace(
      /^(\d+px)/,
      '0',
    );
    Transforms.setNodes(
      editor,
      {
        type,
        style: {
          // ...nodeStyle,
          ...(isFirstChild ? noMarginTopStyles : styles || {}),
        },
        textAlign: '',
      },
      {
        match: (n) => {
          console.log(
            n,
            type === 'paragraph'
              ? nodeType.startsWith('header')
                ? DomEditor.checkNodeType(n, nodeType)
                : DomEditor.checkNodeType(n, 'paragraph')
              : false,
          );
          return type === 'paragraph'
            ? Element.isElement(n) && n.type.startsWith('header')
              ? DomEditor.checkNodeType(n, n.type)
              : DomEditor.checkNodeType(n, 'paragraph')
            : false;
        },
        mode: 'lowest',
      },
    );
    Transforms.setNodes(
      editor,
      {
        type,
        style: {
          ...(isFirstChild ? noMarginTopStyles : styles || {}),
        },
        textAlign: '',
      },
      {
        match: (n) => Element.isElement(n),
        mode: 'highest',
      },
    );
  }
}

export default ClearFormat;
