import { Editor, Transforms, Element } from 'slate';
import { IDomEditor, DomEditor } from '@klein-editor/core';

export function getJustifyType(editor: IDomEditor): string {
  const [match] = Editor.nodes(editor, {
    match: (n) => {
      const { textAlign } = n as any;
      return textAlign;
    },
    universal: true,
  });
  if (!match) return 'left';
  const [n] = match;

  return (n as any).textAlign;
}

