/**
 * 设置 node type （'header1' 'header2' 'paragraph' 等）
 */
export function setJustifyType(editor: IDomEditor, type: string) {
  if (!type) return;

  // 执行命令
  Transforms.setNodes(
    editor,
    {
      textAlign: type,
    },
    { match: (n) => Element.isElement(n) && !editor.isInline(n) }, // inline 元素设置text-align 是没作用的
  );
}
