import { Editor, Element, Node } from 'slate';
import { IDomEditor } from '@klein-editor/core';

/**
 * 获取匹配的 node 节点
 * @param editor editor
 */
export function getMatchNode(editor: IDomEditor): Node | null {
  const [nodeEntry] = Editor.nodes(editor, {
    match: (n) => Element.isElement(n),
    universal: true,
    mode: 'lowest',
  });

  if (!nodeEntry?.length) return null;

  return nodeEntry[0];
}

export const FORMAT_BRUSH_STYLE_MARKS = [
  'lineHeight',
  'color',
  'backgroundColor',
  'background',
  'fontWeight',
  'textAlign',
];

export const FORMAT_BRUSH_TEXT_MARKS = [
  'bold',
  'italic',
  'through',
  'underline',
];
export const FORMAT_BRUSH_NODE_TYPE = [
  'header1',
  'header2',
  'header3',
  'header4',
  'header5',
  'header6',
  'paragraph',
];

/**
 * 获取匹配的样式
 * @param editor editor
 */
export function getMatchStyles(editor: IDomEditor) {
  const style: any = {};

  const marks = Editor.marks(editor) as any;

  const FORMAT_BRUSH_MARKS = [
    ...FORMAT_BRUSH_TEXT_MARKS,
    ...FORMAT_BRUSH_STYLE_MARKS,
  ];
  console.log('marks', marks);
  FORMAT_BRUSH_MARKS.map((mark: string) => {
    const value = marks?.[mark] || marks?.style?.[mark];
    if (value) {
      style[mark] = value;
    }
  });

  const node = getMatchNode(editor) as any;
  const { type: nodeType, style: nodeStyle = {} } = node as any;
  FORMAT_BRUSH_STYLE_MARKS.map((mark: string) => {
    const value = node?.[mark] || node?.style?.[mark];
    if (!style[mark] && value) {
      style[mark] = value;
    }
  });

  return Object.assign(
    {
      type: nodeType,
      lineHeight: '',
      color: 'unset',
      backgroundColor: 'unset',
    },
    style,
    nodeStyle || {},
    {
      fontSize: marks.style?.fontSize
        ? marks.style.fontSize
        : nodeStyle.fontSize || '16px',
    },
  );
}
