/**
 * @description header menu
 *
 */

import { ISelectMenu, IDomEditor, IOption, t } from '@klein-editor/core';
import {
  JUSTIFY_CENTER_SVG,
  JUSTIFY_JUSTIFY_SVG,
  JUSTIFY_LEFT_SVG,
  JUSTIFY_RIGHT_SVG,
} from '../../../constants/icon-svg';
import { getJustifyType, setJustifyType } from '../helper';

class JustifySelectMenu implements ISelectMenu {
  readonly title = t('justifySelect.title');
  readonly iconSvg = JUSTIFY_LEFT_SVG;
  readonly tag = 'select';
  // readonly width = 36;
  readonly selectPanelWidth = 88;
  readonly wide = true;
  readonly setValue = true;

  getOptions(editor: IDomEditor): IOption[] {
    // 基本的 options 列表
    const options = [
      // value 和 elemNode.type 对应
      {
        value: 'left',
        text: JUSTIFY_LEFT_SVG,
        title: '左对齐',
        textIsTag: true,
      },
      {
        value: 'center',
        text: JUSTIFY_CENTER_SVG,
        title: '居中对齐',
        textIsTag: true,
      },
      {
        value: 'right',
        text: JUSTIFY_RIGHT_SVG,
        title: '右对齐',
        textIsTag: true,
      },
      {
        value: 'justify',
        text: JUSTIFY_JUSTIFY_SVG,
        title: '两端对齐',
        textIsTag: true,
      },
    ];

    const curValue = this.getValue(editor).toString();
    options.forEach((opt: IOption) => {
      opt.styleForRenderMenuList = { 'font-size': '0', padding: '8px 36px' };
      if (opt.value === curValue) {
        opt.selected = true;
      } else {
        delete opt.selected;
      }
    });

    return options;
  }

  isActive(editor: IDomEditor): boolean {
    // select menu 会显示 selected value ，用不到 active
    return false;
  }

  /**
   * 获取选中节点的 node.type
   * @param editor editor
   */
  getValue(editor: IDomEditor): string | boolean {
    return getJustifyType(editor);
  }

  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  /**
   * 执行命令
   * @param editor editor
   * @param value node.type
   */
  exec(editor: IDomEditor, value: string | boolean) {
    //【注意】value 是 select change 时获取的，并不是 this.getValue 的值
    setJustifyType(editor, value.toString());
  }
}

export default JustifySelectMenu;
