/**
 * @description parse style html
 *
 */

import { Descendant, Element } from 'slate';
import { IDomEditor } from '@klein-editor/core';
import $, { DOMElement, getStyleValue } from '../../utils/dom';
import { addVnodeStyle, setVnodeStyle } from '../../utils/vdom';
import { ParagraphElement } from './custom-types';
import { toHump } from '../../utils/util';
import { toCHeaderStyle, toBHeaderStyle } from '../header/helper';

export function parseStyleHtml(
  elem: DOMElement,
  node: Descendant,
  editor: IDomEditor,
): Descendant {
  const $elem = $(elem);
  if (!(Element.isElement(node) && node.type === 'paragraph')) return node;

  const elemNode = node as ParagraphElement;

  /**
   * TODO: 待处理无单位场景，例如语雀粘贴内容，font-size值缺少px单位。
   */

  // ['line-height', 'margin', 'font-size', 'font-weight'].forEach((key) => {
  //   const eleNodeKey = toHump(key);
  //   let attr = getStyleValue($elem, key);
  //   /**
  //    * 特殊场景：font-size无单位
  //    */
  //   if (key === 'font-size' && !!attr && /^[0-9]*$/.test(attr)) {
  //     attr = `${attr}px`;
  //   }
  //   elemNode[eleNodeKey] = attr;
  // });

  const { h5Mode } = editor.getConfig();
  const styles = h5Mode
    ? toCHeaderStyle(editor, node.type)
    : toBHeaderStyle(editor, node.type);

  const lineHeight = getStyleValue($elem, 'line-height');
  const margin = getStyleValue($elem, 'margin');
  const fontSize = getStyleValue($elem, 'font-size');
  const fontWeight = getStyleValue($elem, 'font-weight');
  const color = getStyleValue($elem, 'color');
  if (!lineHeight) {
    elemNode.lineHeight = styles.lineHeight;
  }
  if (!margin) {
    elemNode.margin = styles.margin;
  }
  // if (!fontSize) {
  //   elemNode.fontSize = styles.fontSize;
  // }
  // if (!fontWeight) {
  //   elemNode.fontWeight = styles.fontWeight;
  // }
  // if (!color) {
  //   elemNode.color = styles.color;
  // }
  return elemNode;
}
